import React from "react";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";

import {
  DeleteDetailButton,
  HiddenOnPhone,
  InfoWrapper,
  ModalButton,
  StyledOption,
  StyledSection,
  StyledSpan,
  StyledVisualBlockText,
  WrapDetail,
  WrapInfo,
  WrapList,
  WrapToggleDiv,
} from "./ChosenItem.styled";
import VisualModal from "./VisualModal/VisualModal";
import ExampleComponent from "./Example/ExampleComponent";
import { useTranslation } from "react-i18next";

const ChosenItem = ({
  product,
  width,
  detail,
  height,
  edgeSide,
  edgeWidth,
  possibleAmountOfPieces,
  totalPrice,
  maxAmount,
  details,
  selected,
  toggleDetail,
  handleDeleteDetail,
  showButton,
}) => {
  const [scale, setScale] = useState(null);
  const [isOpen, setIsOpen] = useState(false);
  const { ref: myRef, inView: myElIsVisible } = useInView();
  const [memorized, setMemorized] = useState({});
  const { t } = useTranslation("chosenItem");

  useEffect(() => {
    let scaleToFit;

    if (width < 500 && height < 500) {
      scaleToFit = 0.6;
    } else if (width >= 500 && width < 1600 && height >= 500 && height < 1600) {
      scaleToFit = 0.4;
    } else if (
      (width >= 1600 || width < 2700) &&
      (height >= 1600 || height < 2700)
    ) {
      scaleToFit = 0.3;
    }

    setScale(scaleToFit);
  }, [width, height]);

  const toggleModal = () => {
    const section = document.querySelector("section");
    if (!isOpen) {
      section.classList.add("modal-open");
    } else {
      section.classList.remove("modal-open");
    }
    setMemorized({
      width: width,
      height: height,
      scale: scale,
      edgeSide: edgeSide,
    });

    setIsOpen((prev) => !prev);
  };

  return (
    <StyledSection
      $detail={details?.length > 0 ? true : false}
      $open={selected === detail.id ? true : false}
    >
      {details && (
        <WrapToggleDiv onClick={() => toggleDetail(detail.id)}>
          <p>
             {detail.count} {detail.width ? detail.width : 0}  {" "}
            {detail.height ? detail.height : 0} 
          </p>
          <span>{selected === detail.id ? "-" : "+"}</span>
        </WrapToggleDiv>
      )}
      {selected === detail.id ? (
        <>
          <WrapDetail $detail={details?.length > 0 ? true : false}>
            <InfoWrapper $chosenItem={details?.length > 0 ? true : false}>
              <WrapList>
                <WrapInfo>
                  <StyledOption>{t("name")}</StyledOption>
                  <StyledSpan>
                    {t("width")} {product.dimensions.width}  {t("height")}{" "}
                    {product.dimensions.height} 
                  </StyledSpan>
                </WrapInfo>

                <WrapInfo>
                  <StyledOption>{t("thickness")}</StyledOption>
                  <StyledSpan>{product.dimensions.thickness} </StyledSpan>
                </WrapInfo>

                <WrapInfo>
                  <StyledOption>{t("price1")}</StyledOption>
                  <StyledSpan>
                    {" "}
                    {product.offers.price} {product.offers.priceCurrency}
                  </StyledSpan>
                </WrapInfo>
                <WrapInfo>
                  <StyledOption>{t("possibleAmountOfPieces")}</StyledOption>
                  <StyledSpan>
                    {possibleAmountOfPieces ? possibleAmountOfPieces : 0} .
                  </StyledSpan>
                </WrapInfo>

                <WrapInfo>
                  <StyledOption>{t("totalPrice")}</StyledOption>
                  <StyledSpan>{totalPrice ? totalPrice : 0} .</StyledSpan>
                </WrapInfo>
                <WrapInfo>
                  <StyledOption>{t("maxAmount")}</StyledOption>
                  <StyledSpan>{maxAmount ? maxAmount : 0} .</StyledSpan>
                </WrapInfo>
                {edgeWidth ? (
                  <WrapInfo>
                    <StyledOption>{t("edgeWidth")}</StyledOption>

                    <StyledSpan>
                      {edgeWidth !== null ? edgeWidth : 0}
                    </StyledSpan>
                  </WrapInfo>
                ) : null}
                {details?.length > 0 && (
                  <>
                    <WrapInfo>
                      <StyledOption> :</StyledOption>

                      <StyledSpan>
                        {detail.patternDirection === "horizontal"
                          ? ""
                          : ""}
                      </StyledSpan>
                    </WrapInfo>
                    <WrapInfo>
                      <StyledOption>   :</StyledOption>
                      <StyledSpan>{detail.comment}</StyledSpan>
                    </WrapInfo>
                  </>
                )}
              </WrapList>
              {details && (
                <DeleteDetailButton
                  type="button"
                  onClick={() => handleDeleteDetail(detail.id)}
                >
                  {t("deleteButton")}
                </DeleteDetailButton>
              )}
            </InfoWrapper>

            <HiddenOnPhone ref={myRef}>
              <StyledVisualBlockText>{t("cut")}</StyledVisualBlockText>
              <ExampleComponent
                width={width}
                height={height}
                scale={scale}
                edgeSide={edgeSide}
              />
            </HiddenOnPhone>
          </WrapDetail>
          {!myElIsVisible ? (
            <ModalButton onClick={toggleModal}>{t("cut")}</ModalButton>
          ) : null}
          {isOpen ? (
            <VisualModal memorized={memorized} close={toggleModal} />
          ) : null}
        </>
      ) : null}
    </StyledSection>
  );
};

export default ChosenItem;
